:export {
  xs: 600;
  sm: 960;
  md: 1279;
  lg: 1600;
}

#mainMenuComponent {
  width: 100%;
}

#mainMenu {
  background-color: #fefefe;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 8rem;
  max-width: 100%;
  box-shadow: 4px 4px 40px 0px #ff0000e5, 0px 4px 4px 0px #00000040;
  border-radius: 16px;
  margin: 0 20px;
}

div .active {
  color: #202020;
}
div .active::after {
  content: "";
  border-radius: 4px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 4px 4px 40px rgba(255, 0, 0, 0.9);
  background-color: #df4b4b;
  clip-path: inset(0 0 0 0);
}

.options {
  position: relative;
  color: #979797;
  font-size: 2em;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
  user-select: none;
}
.options:hover {
  color: #ff0000e5;
  cursor: pointer;
}
.options::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  z-index: 2;
  display: block;
  width: 100%;
  height: 0.5rem;
  background-color: #fe6666;
  clip-path: inset(0 100% 0 0);
  transition: all 0.5s ease-in-out;
}