:export {
  xs: 600;
  sm: 960;
  md: 1279;
  lg: 1600;
}

#container {
  position: relative;
  background-color: #fefefe;
  border-radius: 16px;
  max-width: 100%;
  margin: 2rem;
  font-weight: 500;
  height: 100%;
}

#title {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  font-size: 3rem;
  font-weight: bold;
  color: #000;
}
#title::before {
  content: "";
  background-image: url("../../../static/assets/ui/ModeRules.png");
  background-size: cover;
  width: 2rem;
  height: 2rem;
  margin: auto 15px auto 0;
}

#content {
  height: 80%;
  display: flex;
  font-size: 1.5rem;
  margin: 0 2rem;
  justify-content: center;
  align-items: center;
}